/**
 *     comparisonArr: any[],
 *     comparisonTitle?: string,//
 columnOneTitle?: string,//
 columnOneTitleKey?: string,//key
 columnLastTitle?: string,//
 columnLastTitleKey?: string//key
 * */
import {connect} from "dva";
import {Table} from "antd";
import * as React from "react";
import {Component} from "react";
import './comparison.scss'
import {DifferenceJudge} from "./../../../utils/index"

type PageOwnProps = {
    data: any,
    bureauEducation: any
}

type PageState = {
    columns: any[]
}

@connect(({bureauEducation}: any) => ({
    bureauEducation
}))

class Comparison extends Component <PageOwnProps, PageState> {

    constructor(props: any) {
        super(props);
        this.state = {
            columns: []
        }
    }

    componentDidMount(): void {
        this.setState({
            columns: [
                {
                    title: this.props.data.columnOneTitle,
                    dataIndex: this.props.data.columnOneTitleKey,
                    key: 0,
                    width: 93,
                    fixed: 'left',
                    render: (text: string) => <span className={'td-school-name'}>
                        {text}
                    </span>
                },
                {
                    title: '',
                    dataIndex: 'average',
                    key: 1,
                    width: 82,
                    render: (text: string, record: any,) => {
                        let differenceJudge = new DifferenceJudge(record.difference);//
                        let resultClassName: string = differenceJudge.goJudge();//span
                        return <span className={resultClassName}>
                        {text}
                    </span>
                    }
                },
                {
                    title: '',
                    dataIndex: 'difference',
                    key: 2,
                    width: 82,
                    align: 'center',
                    render: (text: string, record: any,) => {
                        let differenceJudge = new DifferenceJudge(record.difference, true);//
                        let resultClassName: string = differenceJudge.goJudge();//span
                        return <span className={resultClassName}>
                        {text}
                    </span>
                    },
                    sorter: (a: any, b: any) => a.difference - b.difference,
                },
                {
                    title: '',
                    dataIndex: 'ranking',
                    key: 3,
                    width: 82,
                    align: 'center'
                },
                {
                    title: '',
                    dataIndex: 'standard',
                    key: 4,
                    width: 82,
                    align: 'center',
                    render: (text: string) => <span className={'td-standard'}>
                        {text}
                    </span>
                },
                {
                    title: this.props.data.columnLastTitle,
                    dataIndex: this.props.data.columnLastTitleKey,
                    key: 5,
                    width: 96,
                    align: 'center',
                    render: (text: string, record: any) => {
                        return this.props.data.comparisonTitle === '' ?
                            <p className={'table-full'}>{text}<span>{record.overall}</span></p>
                            : <span className={'td-standard'}>{text}</span>
                    }
                }]
        })
    }

    componentWillUnmount(): void {
        this.setState({
            columns: []
        })
    }

    public render() {
        return (
            <div>
                {this.props.data.comparisonArr && this.props.data.comparisonArr.length ?
                    <div className={'school-comparison'}>
                        <h5 className={'box-title'}>
                            {`${this.props.data.comparisonTitle}`}
                        </h5>

                        <Table dataSource={this.props.data.comparisonArr}
                               columns={this.state.columns}
                               bordered
                               className={'wathet-table'}
                               size={"middle"}
                               scroll={{x: 517}}
                               pagination={false}
                               rowClassName={(record, index) => index % 2 ? 'table-row-zebra' : 'table-row-normal'}
                        >
                        </Table>

                        <p>
                            
                        </p>
                    </div> : null}
            </div>)
    }
}

export default Comparison;





